<script lang="babel">
  export default{
    name: 'tAuditStore',
    data(){
      return{
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        date:'',
        records:[],
        count:0,
        total:0,
        query:{
          pageNo:1,
          pageSize:10,
          userNo:"",//
          userName:"",  //
          settleNo:"",//
          userType:2,//(23)
          starDate:"", // 
          endDate:"", //
          settleStatus:''
        },
        multipleSelection:[],
        currentPage4: 1,
        loading:true,
        isShow:false,
        index:'',
      }
    },
    created: function () {
      this.getData()
    },
    methods: {
      changeTime: function (row, column) {
        var val = row.settleDate;
        if (val == '' || val == null) {
          return ''
        } else {
          val = new Date(val)
          var year = val.getFullYear();
          var month = val.getMonth() + 1;
          var date = val.getDate();
          var hour = val.getHours();
          var minute = val.getMinutes();
          var second = val.getSeconds();

          function tod(a) {
            if (a < 10) {
              a = "0" + a
            }
            return a;
          }

          return year + "-" + tod(month) + "-" + tod(date);
        }
      },
      changeNum: function (row, column) {
        var val = row.settleAmount;
        return parseFloat(val).toFixed(2);
      },
      getData: function () {
        this.loading = true;
        this.$http.post('/admin/settlementRecord/list',this.$data.query)
          .then(function (res) {
            this.$data.records = res.data.records;
            this.$data.count = res.data.count;
            this.$data.total = res.data.totalPage;
            this.$data.loading = false;
            var changeTime=function (val) {
              if(val==''||val==null){
                return ''
              }else {
                val = new Date(val)
                var year=val.getFullYear();
                var month=val.getMonth()+1;
                var date=val.getDate();
                function tod(a) {
                  if(a<10){
                    a = "0"+a
                  }
                  return a;
                }
                return year+"-"+tod(month)+"-"+tod(date);
              }
            }
            for(let i = 0; i < this.$data.records.length; i++){
              this.$data.records[i].tradeDate = changeTime(this.$data.records[i].tradeDate)
            }
          },function (err) {
            this.$data.loading = false;
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            })
          })
      },
      search: function () {
        this.$data.query.pageNo = 1;
        this.getData()
      },
      list: function (val) {
        this.$data.index = val;
        this.$data.isShow = true;
      },
      //
      handleSelectionChange(val) {
        console.log(val)
        this.multipleSelection = val;
      },
      //
      handleSizeChange(val) {
        this.$data.query.pageNo = 1;
        this.$data.query.pageSize = val;
        this.getData()
      },
      //
      handleCurrentChange(val) {
        this.$data.query.pageNo = val;
        this.getData()
      },
      //
      settle(val,id) {
        this.$http.post('/admin/settle/singleSettle',{recordId:id,option:val})
          .then(function (res) {
            this.$message({
              showClose: true,
              message: '',
              type: 'success'
            })
            this.$data.isShow = false
          })
          .catch(function (err) {
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            })
          })
      }
    },
    watch:{
      date:function (val,oldVal) {
        if(val[0]!=null){
          for(var j=0;j<val.length;j++){
            var str = val[j];
            var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
            for(var i = 0, len = ary.length; i < len; i ++) {
              if(ary[i] < 10) {
                ary[i] = '0' + ary[i];
              }
            }
            str = ary[0] + '-' + ary[1] + '-' + ary[2];
            if(j==0){
              this.$data.query.starDate = str;
            }else {
              this.$data.query.starDate = str;
            }
          }
        }else {
          this.$data.query.starDate = '';
          this.$data.query.starDate = '';
        }
      }
    }
  }
</script>
